import React, { useEffect, useState,useRef } from 'react';
import { connect } from 'react-redux';
import Part from '../part/part'
// import { useDrag } from 'react-dnd'
import { removeSection, setCollapseOpen, sectionInEditing,addSection2 } from '../../redux/actions/funnel.action'
import './section.css'
import PdfContainer from '../../components/pdf/pdfContainer';

import GridList from '@material-ui/core/GridList';
import Grid  from '@material-ui/core/Grid';
function Section(props) {
    const { section, indexSection, removeSection, setCollapseOpen, sectionInEditing, editMode, jsonPage, numSections, message, saveInLocalStorage, urlPage, nameUser, nameFunnel,addSection2 } = props
    const bodyRef = useRef();
    function addNewGrid(numParts,styleG,place) {
        let newSection = {
            settings: { style: { backgroundColor: '' } },
            arrParts: [],
            id: numSections
        }


        for (let i = 0; i < numParts; i++) {
            newSection.arrParts.push({
                settings: { style: {} },
                arrElements: [
                ]
            })
        }
        // props.iframe.contentWindow.postMessage(JSON.stringify({ function: "add section", params: newSection }), '*')
        addSection2(newSection, numSections,styleG,place)
        props.changeMessage()
    }


    const editSection = (indexSection) => {
        debugger
        //////////לשים משהוא במקום השליחה הזו
        // window.parent.postMessage(JSON.stringify({function:"set collapse open" ,params:{elementOrSection:'section',id:indexSection}}), '*')
        console.log(jsonPage)
        //sectionInEditing(indexSection)
        //setCollapseOpen('section')
    }
 
    console.log(props.section)
    let gt=props.section.gridType
    console.log(gt)
    console.log(section.settings.style)
    return (
        <div  style={{height:'100%',marginBottom:'0.5px'}}>
            <div id="taille" className={editMode ? "row no-gutters sectionToEdit" : "row no-gutters"} style={section.settings.style} >
        {/* <div ref={drag} className="d-flex pl-1 move" onDrag={e => drag1(e)} draggable="true" onDragStart="drag(event)" > */}
                {editMode && <div className='edit_buttons_section'>
                    <div>
                        <span class="material-icons" style={{ zIndex: 3, fontSize: '1.2rem' }} onClick={() => removeSection(indexSection)}>
                            delete
                </span>
                    </div>
                    <div><PdfContainer reference={bodyRef} fileName={`funnel1.pdf`} /></div>
                    <div>
                        <span class="material-icons" style={{ zIndex: 4, fontSize: '1.2rem' }} onClick={() => editSection(indexSection)} >
                            create
                    </span>
                    </div></div>}
            <Grid container direction="row" ref={bodyRef} style={{height:"100%"}} id={gt=='i'? 'tmpg': (gt=='j')?'tmpg1':""}>

                { 
                props.section.arrParts.map((item, i) => {
                    return (
                        
                        // <div key={i} className={(gt=='b'|| gt=='d') ?`col-md-${12 / props.section.arrParts.length} p-0` : (gt=='a'|| gt=='c'||gt=='e') ?`row-md p-0` :""} style={{clear:'both'}}>
                        //     <Part part={item} indexSection={indexSection} indexPart={i}></Part>
                            
                        // </div>
                        <>
                        {
                            (gt=='b'|| gt=='d')? <>
                            <div key={i} className={`col-md-${12 / props.section.arrParts.length} p-0` } id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='25vh'gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div> </>
                            :  (gt=='a'|| gt=='c'||gt=='e') ? <>
                            <Grid container direction="row" key={i} id={`mypart${indexSection}`}>
                            <Part part={item} indexSection={indexSection} indexPart={i} taille={gt=='a'?'12vh':(gt=='c'?'9vh':'25vh') }   gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </Grid>
                            </> : (gt=='f')? <>
                            <div key={i} className={i==props.section.arrParts.length-1? `col-md-6 p-0`: `col-md-3 p-0`}id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='25vh' gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div> 
                            </> :
                             (gt=='g')?
                             <>
                           
                             {i==((props.section.arrParts.length-1)/2) ?
                             <>
                            <div key={i} className={`w-100`}/>
                            <div key={i} className={`col-md-${12 / 2} p-0`} id={`mypart${indexSection}`}>
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='10vh'  gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div>

                             </>:
                             <>
                            <div key={i} className={`col-md-${12 / 2} p-0`} id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='10vh' gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div>
                             </>
                            }
                             </>
                             : (gt=='h')? <>
                            {(i==3|| i==6 )?
                             <>
                            <div key={i} className={`w-100`}/>
                            <div key={i} className={`col-md-${12 / 3} p-0`} id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='8vh' gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid}  />
                            </div>

                             </>:
                             <>
                            <div key={i} className={`col-md-${12 / 3} p-0`} id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='8vh' gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div>
                             </>
                            }   
                             </> :(gt=='i')? 
                             <>
                            <div key={i} className={`tm${i}`} id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='11vh' gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div>

                            </>
                         
                        :<>
                            <div key={i} className={`tm${i}`} id={`mypart${indexSection}`} >
                            <Part part={item} indexSection={indexSection} indexPart={i} taille='11vh' gt={props.section.gridType} nPart={props.section.arrParts.length} addNewGrid={addNewGrid} />
                            </div>
                        </>
                        }
                        </>
                    )
                })
               
                }
            </Grid >
                {/* </div> */}
            </div>
        </div>
    )
}
export default connect(
    (state) => {
        return {
            editMode: state.funnel.editMode,
            jsonPage: state.funnel.jsonPage,
            numSections: state.funnel.jsonPage.num_sections,
            urlPage: state.funnel.urlPage,
            nameUser: state.user.userName,
            nameFunnel: state.funnel.name,
            message: state.funnel.message
        }
    },
    (dispatch) => {
        return {
            removeSection: function (indexSection) {
                dispatch(removeSection(indexSection))
            },
            saveInLocalStorage: () => { dispatch({ type: '[funnel] SAVE_IN_LOCAL_STORAGE' }) },
            setCollapseOpen: function (collapse) { dispatch(setCollapseOpen(collapse)) },
            sectionInEditing: (indexSection) => { dispatch(sectionInEditing(indexSection)) },
            addSection2: function (newSection,id,s,place) { dispatch(addSection2(newSection,id,s,place)) },
            changeMessage: () => { dispatch({ type: '[funnel] CHANGE_MESSAGE' }) },

        }
    }
)(Section)
